import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';

function BehaviorTest() {
  const navigate = useNavigate();
  const [clickCount, setClickCount] = useState(0);
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    message: ''
  });
  const [scrollPosition, setScrollPosition] = useState(0);

  // 监听滚动事件
  useEffect(() => {
    const handleScroll = () => {
      setScrollPosition(window.pageYOffset);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // 模拟业务操作 - 购物车
  const simulateShoppingCart = () => {
    (window as any).monitor?.trackBusiness('add_to_cart', {
      productId: 'PROD-001',
      productName: '测试商品',
      quantity: 1,
      price: 99.99
    });

    (window as any).monitor?.trackEvent('user', '模拟添加购物车操作', {
      product: 'PROD-001',
      timestamp: Date.now()
    });
  };

  // 模拟用户登录流程
  const simulateLogin = () => {
    (window as any).monitor?.trackBusiness('login_attempt', {
      method: 'email',
      timestamp: Date.now()
    });

    // 模拟登录成功
    setTimeout(() => {
      (window as any).monitor?.trackBusiness('login_success', {
        userId: 'USER-123',
        loginTime: Date.now()
      });
    }, 1000);
  };

  // 模拟支付流程
  const simulatePayment = () => {
    const steps = ['initiate_payment', 'select_method', 'confirm_payment', 'payment_success'];

    steps.forEach((step, index) => {
      setTimeout(() => {
        (window as any).monitor?.trackBusiness(step, {
          orderId: 'ORDER-456',
          amount: 199.98,
          step: index + 1,
          totalSteps: steps.length
        });
      }, index * 500);
    });
  };

  // 表单提交处理
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    (window as any).monitor?.trackEvent('user', '表单提交', {
      formType: 'contact',
      fields: Object.keys(formData),
      timestamp: Date.now()
    });

    alert('表单提交成功！请查看控制台和网络面板。');
  };

  // 创建大量可点击元素来测试点击追踪
  const createClickableElements = () => {
    const elements = [];
    for (let i = 0; i < 20; i++) {
      elements.push(
        <button
          key={i}
          className="test-button"
          style={{ margin: '2px' }}
          onClick={() => {
            setClickCount(prev => prev + 1);
            (window as any).monitor?.trackEvent('user', `点击按钮 #${i + 1}`, {
              buttonId: i + 1,
              totalClicks: clickCount + 1
            });
          }}
        >
          按钮 {i + 1}
        </button>
      );
    }
    return elements;
  };

  return (
    <div className="test-section">
      <h2>👤 用户行为追踪测试</h2>

      <div style={{ marginBottom: '20px' }}>
        <Link to="/">
          <button className="test-button">← 返回首页</button>
        </Link>
        <button
          className="test-button"
          onClick={() => navigate('/about')}
        >
          跳转到关于页面 (路由监控)
        </button>
      </div>

      <div className="test-status info">
        📊 当前统计: 点击次数 {clickCount} | 滚动位置 {scrollPosition.toFixed(0)}px
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🛍️ 业务事件模拟</h3>
        <button className="test-button" onClick={simulateShoppingCart}>
          模拟购物车操作
        </button>
        <button className="test-button" onClick={simulateLogin}>
          模拟用户登录
        </button>
        <button className="test-button" onClick={simulatePayment}>
          模拟支付流程
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🖱️ 点击行为测试</h3>
        <p>点击下面的按钮测试点击事件追踪：</p>
        <div style={{ display: 'flex', flexWrap: 'wrap', gap: '4px' }}>
          {createClickableElements()}
        </div>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>📝 表单交互测试</h3>
        <form onSubmit={handleSubmit} style={{ maxWidth: '400px' }}>
          <div style={{ marginBottom: '12px' }}>
            <label style={{ display: 'block', marginBottom: '4px' }}>用户名:</label>
            <input
              type="text"
              value={formData.username}
              onChange={(e) => setFormData(prev => ({ ...prev, username: e.target.value }))}
              style={{ width: '100%', padding: '8px', borderRadius: '4px', border: '1px solid #ddd' }}
              placeholder="输入用户名"
            />
          </div>

          <div style={{ marginBottom: '12px' }}>
            <label style={{ display: 'block', marginBottom: '4px' }}>邮箱:</label>
            <input
              type="email"
              value={formData.email}
              onChange={(e) => setFormData(prev => ({ ...prev, email: e.target.value }))}
              style={{ width: '100%', padding: '8px', borderRadius: '4px', border: '1px solid #ddd' }}
              placeholder="输入邮箱地址"
            />
          </div>

          <div style={{ marginBottom: '12px' }}>
            <label style={{ display: 'block', marginBottom: '4px' }}>留言:</label>
            <textarea
              value={formData.message}
              onChange={(e) => setFormData(prev => ({ ...prev, message: e.target.value }))}
              style={{ width: '100%', padding: '8px', borderRadius: '4px', border: '1px solid #ddd', height: '80px' }}
              placeholder="输入留言内容"
            />
          </div>

          <button type="submit" className="test-button">
            提交表单
          </button>
        </form>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>⌨️ 键盘事件测试</h3>
        <input
          type="text"
          placeholder="在这里输入文字测试键盘事件追踪"
          style={{ width: '100%', padding: '12px', borderRadius: '4px', border: '1px solid #ddd' }}
          onKeyDown={(e) => {
            if (e.key === 'Enter') {
              (window as any).monitor?.trackEvent('user', '键盘 Enter 键按下', {
                inputValue: e.currentTarget.value,
                timestamp: Date.now()
              });
            }
          }}
        />
        <p style={{ fontSize: '12px', color: '#666' }}>
          💡 按 Enter 键会触发特殊的键盘事件记录
        </p>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>📏 滚动行为测试</h3>
        <p>滚动页面测试滚动事件追踪（当前位置: {scrollPosition.toFixed(0)}px）</p>
        <div style={{ height: '300px', background: '#f9f9f9', overflowY: 'scroll', padding: '16px' }}>
          <div style={{ height: '800px' }}>
            <h4>滚动测试区域</h4>
            <p>这是一个可滚动的区域，用于测试滚动事件的捕获。</p>
            {[...Array(20)].map((_, i) => (
              <p key={i} style={{ margin: '16px 0' }}>
                📜 滚动内容行 {i + 1} - 继续滚动测试滚动事件追踪功能
              </p>
            ))}
          </div>
        </div>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🎯 自定义用户操作</h3>
        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackEvent('user', '开始购物流程', {
              step: 'browse_products',
              category: 'electronics',
              timestamp: Date.now()
            });
          }}
        >
          开始购物流程
        </button>

        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackEvent('user', '查看商品详情', {
              productId: 'PROD-999',
              productName: '智能手机',
              price: 2999,
              timestamp: Date.now()
            });
          }}
        >
          查看商品详情
        </button>

        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackEvent('user', '用户反馈', {
              type: 'suggestion',
              rating: 5,
              feedback: '界面很好用',
              timestamp: Date.now()
            });
          }}
        >
          提交用户反馈
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>⏱️ 页面停留时间测试</h3>
        <p>在此页面停留一段时间，然后跳转到其他页面测试停留时间记录。</p>
        <button
          className="test-button"
          onClick={() => {
            // 手动记录页面访问
            (window as any).monitor?.trackEvent('navigation', '手动记录页面访问', {
              page: 'behavior-test',
              action: 'manual_visit_record',
              timestamp: Date.now()
            });
          }}
        >
          手动记录页面访问
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🔄 复杂交互流程测试</h3>
        <button
          className="test-button"
          onClick={() => {
            // 模拟一个完整的用户操作流程
            const steps = [
              { action: '进入商品页面', delay: 0 },
              { action: '查看商品图片', delay: 500 },
              { action: '选择商品规格', delay: 1000 },
              { action: '添加到购物车', delay: 1500 },
              { action: '跳转到购物车', delay: 2000 },
            ];

            steps.forEach((step, index) => {
              setTimeout(() => {
                (window as any).monitor?.trackEvent('user', step.action, {
                  flowStep: index + 1,
                  totalSteps: steps.length,
                  flowId: 'shopping_flow_001',
                  timestamp: Date.now()
                });
              }, step.delay);
            });
          }}
        >
          执行复杂用户流程
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🎨 UI 交互测试</h3>
        <div style={{ display: 'flex', gap: '8px', flexWrap: 'wrap' }}>
          <button
            className="test-button"
            onMouseEnter={() => {
              (window as any).monitor?.trackEvent('user', '鼠标悬停', {
                element: 'hover-button',
                timestamp: Date.now()
              });
            }}
          >
            悬停测试
          </button>

          <button
            className="test-button"
            onFocus={() => {
              (window as any).monitor?.trackEvent('user', '获得焦点', {
                element: 'focus-button',
                timestamp: Date.now()
              });
            }}
          >
            焦点测试
          </button>

          <button
            className="test-button"
            onContextMenu={(e) => {
              e.preventDefault();
              (window as any).monitor?.trackEvent('user', '右键点击', {
                element: 'context-menu-button',
                timestamp: Date.now()
              });
            }}
          >
            右键测试
          </button>
        </div>
      </div>

      <div>
        <h3>📊 观察要点</h3>
        <ul style={{ textAlign: 'left', lineHeight: '1.6', fontSize: '14px' }}>
          <li><strong>面包屑轨迹:</strong> 查看用户操作是否按时间序列正确记录</li>
          <li><strong>事件分类:</strong> 不同类型的事件是否正确分类（user、navigation等）</li>
          <li><strong>数据完整性:</strong> 事件数据是否包含必要的上下文信息</li>
          <li><strong>时间准确性:</strong> 事件时间戳是否准确</li>
          <li><strong>路由监控:</strong> 页面跳转是否被正确记录</li>
          <li><strong>批量上报:</strong> 多个事件是否会被批量发送</li>
          <li><strong>页面停留:</strong> 页面切换时是否记录停留时间</li>
        </ul>
      </div>

      {/* 增加页面高度，便于测试滚动 */}
      <div style={{ height: '400px', background: 'linear-gradient(to bottom, #f0f0f0, #e0e0e0)', padding: '20px', marginTop: '40px' }}>
        <h4>📏 额外滚动区域</h4>
        <p>这个区域用于测试页面滚动监控功能。向下滚动查看更多内容。</p>
        {[...Array(15)].map((_, i) => (
          <div key={i} style={{ margin: '20px 0', padding: '16px', background: 'white', borderRadius: '4px' }}>
            <h5>内容块 {i + 1}</h5>
            <p>这是用于测试滚动行为的内容块。SDK 应该能够记录用户的滚动进度和停留时间。</p>
            {i % 5 === 4 && (
              <button
                className="test-button"
                onClick={() => {
                  (window as any).monitor?.trackEvent('user', `滚动区域中的点击 - 块${i + 1}`, {
                    scrollPosition: window.pageYOffset,
                    blockIndex: i + 1,
                    timestamp: Date.now()
                  });
                }}
              >
                点击记录位置
              </button>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

export default BehaviorTest;