import React, { useState, useEffect } from 'react';

interface UserData {
  id: string;
  name: string;
  email: string;
  loginTime: number;
  permissions: string[];
}

const UserDashboard: React.FC = () => {
  const [userData, setUserData] = useState<UserData | null>(null);
  const [loading, setLoading] = useState(true);
  const [notifications, setNotifications] = useState<string[]>([]);

  useEffect(() => {
    // 模拟加载用户数据
    const loadUserData = async () => {
      try {
        setLoading(true);

        // 模拟API延迟
        await new Promise(resolve => setTimeout(resolve, 1000));

        // 模拟用户数据
        setUserData({
          id: 'user_' + Date.now(),
          name: '测试用户',
          email: 'test@example.com',
          loginTime: Date.now(),
          permissions: ['read', 'write', 'admin']
        });

        setNotifications([
          '欢迎使用监控平台',
          '您有3条新消息',
          '系统将在今晚进行维护'
        ]);

      } catch (error) {
        console.error('加载用户数据失败:', error);
      } finally {
        setLoading(false);
      }
    };

    loadUserData();
  }, []);

  const handleLogout = () => {
    console.log('用户退出');
    setUserData(null);
    setNotifications([]);
  };

  const handleDataExport = async () => {
    try {
      console.log('开始导出数据');

      // 模拟数据导出
      const exportData = {
        user: userData,
        exportTime: new Date().toISOString(),
        type: 'user_dashboard_export'
      };

      // 模拟API调用
      const response = await fetch('/api/export/user-data', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(exportData)
      });

      if (!response.ok) {
        throw new Error('导出失败');
      }

      console.log('数据导出成功');
    } catch (error) {
      console.error('数据导出失败:', error);
    }
  };

  if (loading) {
    return (
      <div className="dashboard-loading" id="dashboard-loading">
        <div id="loading-spinner">正在加载用户数据...</div>
      </div>
    );
  }

  return (
    <div className="user-dashboard" id="user-dashboard-container">
      <header className="dashboard-header" id="dashboard-header">
        <h2 id="dashboard-title">用户控制面板</h2>
        <button
          id="logout-button"
          className="logout-btn"
          onClick={handleLogout}
          data-action="logout"
        >
          退出登录
        </button>
      </header>

      {userData && (
        <>
          <section className="user-info-section" id="user-info-section">
            <h3 id="user-info-title">用户信息</h3>
            <div className="user-info-card" id="user-info-card">
              <div className="info-item" id="user-id-info">
                <label>用户ID:</label>
                <span id="user-id-value">{userData.id}</span>
              </div>
              <div className="info-item" id="user-name-info">
                <label>姓名:</label>
                <span id="user-name-value">{userData.name}</span>
              </div>
              <div className="info-item" id="user-email-info">
                <label>邮箱:</label>
                <span id="user-email-value">{userData.email}</span>
              </div>
              <div className="info-item" id="user-login-time-info">
                <label>登录时间:</label>
                <span id="user-login-time-value">
                  {new Date(userData.loginTime).toLocaleString()}
                </span>
              </div>
            </div>
          </section>

          <section className="permissions-section" id="permissions-section">
            <h3 id="permissions-title">权限列表</h3>
            <div className="permissions-list" id="permissions-list">
              {userData.permissions.map((permission, index) => (
                <span
                  key={permission}
                  className="permission-tag"
                  id={`permission-${permission}`}
                  data-permission={permission}
                >
                  {permission}
                </span>
              ))}
            </div>
          </section>

          <section className="notifications-section" id="notifications-section">
            <h3 id="notifications-title">通知消息</h3>
            <div className="notifications-list" id="notifications-list">
              {notifications.map((notification, index) => (
                <div
                  key={index}
                  className="notification-item"
                  id={`notification-${index}`}
                  data-notification-index={index}
                >
                  {notification}
                </div>
              ))}
            </div>
          </section>

          <section className="actions-section" id="dashboard-actions-section">
            <h3 id="actions-title">操作</h3>
            <div className="action-buttons" id="action-buttons">
              <button
                id="export-data-button"
                className="action-btn primary"
                onClick={handleDataExport}
                data-action="export"
              >
                导出数据
              </button>

              <button
                id="clear-notifications-button"
                className="action-btn secondary"
                onClick={() => {
                  setNotifications([]);
                  console.log('通知已清空');
                }}
                data-action="clear-notifications"
              >
                清空通知
              </button>

              <button
                id="refresh-dashboard-button"
                className="action-btn"
                onClick={() => {
                  window.location.reload();
                }}
                data-action="refresh"
              >
                刷新面板
              </button>
            </div>
          </section>
        </>
      )}
    </div>
  );
};

export default UserDashboard;