import React, { useState } from 'react';

interface Product {
  id: string;
  name: string;
  price: number;
  quantity: number;
  category: string;
}

const ShoppingCart: React.FC = () => {
  const [products, setProducts] = useState<Product[]>([
    {
      id: 'prod-001',
      name: '监控服务套餐A',
      price: 99.99,
      quantity: 1,
      category: 'service'
    },
    {
      id: 'prod-002',
      name: '高级分析报告',
      price: 199.99,
      quantity: 1,
      category: 'analytics'
    }
  ]);

  const [couponCode, setCouponCode] = useState('');
  const [isProcessing, setIsProcessing] = useState(false);

  const updateQuantity = (productId: string, newQuantity: number) => {
    if (newQuantity <= 0) {
      removeProduct(productId);
      return;
    }

    setProducts(prev =>
      prev.map(product =>
        product.id === productId
          ? { ...product, quantity: newQuantity }
          : product
      )
    );
  };

  const removeProduct = (productId: string) => {
    setProducts(prev => prev.filter(product => product.id !== productId));
    console.log(`产品 ${productId} 已从购物车移除`);
  };

  const addProduct = () => {
    const newProduct: Product = {
      id: `prod-${Date.now()}`,
      name: `新产品 ${Date.now()}`,
      price: Math.floor(Math.random() * 500) + 50,
      quantity: 1,
      category: 'addon'
    };

    setProducts(prev => [...prev, newProduct]);
    console.log('新产品已添加到购物车:', newProduct);
  };

  const applyCoupon = async () => {
    if (!couponCode.trim()) {
      alert('请输入优惠券代码');
      return;
    }

    try {
      console.log('应用优惠券:', couponCode);

      // 模拟API调用
      const response = await fetch('/api/coupons/validate', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          code: couponCode,
          cartTotal: getTotalPrice()
        })
      });

      if (!response.ok) {
        throw new Error('优惠券无效');
      }

      alert('优惠券应用成功！');
    } catch (error) {
      console.error('优惠券应用失败:', error);
      alert('优惠券应用失败，请检查代码是否正确');
    }
  };

  const checkout = async () => {
    if (products.length === 0) {
      alert('购物车为空');
      return;
    }

    setIsProcessing(true);

    try {
      console.log('开始结账流程');

      // 模拟支付处理
      const orderData = {
        products: products,
        total: getTotalPrice(),
        couponCode: couponCode,
        timestamp: Date.now()
      };

      const response = await fetch('/api/orders/create', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(orderData)
      });

      if (!response.ok) {
        throw new Error('订单创建失败');
      }

      // 模拟支付延迟
      await new Promise(resolve => setTimeout(resolve, 2000));

      alert('支付成功！订单已创建');
      setProducts([]);
      setCouponCode('');

    } catch (error) {
      console.error('结账失败:', error);
      alert('支付失败，请重试');
    } finally {
      setIsProcessing(false);
    }
  };

  const getTotalPrice = () => {
    return products.reduce((total, product) => total + (product.price * product.quantity), 0);
  };

  return (
    <div className="shopping-cart" id="shopping-cart-container">
      <header className="cart-header" id="cart-header">
        <h2 id="cart-title">购物车</h2>
        <span id="cart-item-count" className="item-count">
          {products.length} 个商品
        </span>
      </header>

      <section className="cart-content" id="cart-content">
        {products.length === 0 ? (
          <div className="empty-cart" id="empty-cart-message">
            <p>购物车为空</p>
            <button
              id="add-sample-product"
              className="add-btn"
              onClick={addProduct}
              data-action="add-product"
            >
              添加示例商品
            </button>
          </div>
        ) : (
          <>
            <div className="products-list" id="products-list">
              {products.map((product, index) => (
                <div
                  key={product.id}
                  className="product-item"
                  id={`product-item-${product.id}`}
                  data-product-id={product.id}
                  data-product-category={product.category}
                >
                  <div className="product-info" id={`product-info-${product.id}`}>
                    <h4 id={`product-name-${product.id}`}>{product.name}</h4>
                    <span id={`product-price-${product.id}`} className="price">
                      ¥{product.price.toFixed(2)}
                    </span>
                    <span id={`product-category-${product.id}`} className="category">
                      {product.category}
                    </span>
                  </div>

                  <div className="quantity-controls" id={`quantity-controls-${product.id}`}>
                    <button
                      id={`decrease-quantity-${product.id}`}
                      className="quantity-btn"
                      onClick={() => updateQuantity(product.id, product.quantity - 1)}
                      data-action="decrease-quantity"
                      data-product-id={product.id}
                    >
                      -
                    </button>

                    <span id={`quantity-value-${product.id}`} className="quantity">
                      {product.quantity}
                    </span>

                    <button
                      id={`increase-quantity-${product.id}`}
                      className="quantity-btn"
                      onClick={() => updateQuantity(product.id, product.quantity + 1)}
                      data-action="increase-quantity"
                      data-product-id={product.id}
                    >
                      +
                    </button>
                  </div>

                  <button
                    id={`remove-product-${product.id}`}
                    className="remove-btn"
                    onClick={() => removeProduct(product.id)}
                    data-action="remove-product"
                    data-product-id={product.id}
                  >
                    移除
                  </button>
                </div>
              ))}
            </div>

            <div className="cart-actions" id="cart-actions">
              <button
                id="add-more-products"
                className="add-btn secondary"
                onClick={addProduct}
                data-action="add-product"
              >
                添加更多商品
              </button>
            </div>
          </>
        )}
      </section>

      {products.length > 0 && (
        <section className="cart-summary" id="cart-summary">
          <div className="coupon-section" id="coupon-section">
            <h3 id="coupon-title">优惠券</h3>
            <div className="coupon-input-group" id="coupon-input-group">
              <input
                type="text"
                id="coupon-code-input"
                placeholder="输入优惠券代码"
                value={couponCode}
                onChange={(e) => setCouponCode(e.target.value)}
                data-field="coupon-code"
              />
              <button
                id="apply-coupon-button"
                className="apply-coupon-btn"
                onClick={applyCoupon}
                data-action="apply-coupon"
              >
                应用
              </button>
            </div>
          </div>

          <div className="total-section" id="total-section">
            <div className="total-line" id="subtotal-line">
              <span>小计:</span>
              <span id="subtotal-amount">¥{getTotalPrice().toFixed(2)}</span>
            </div>
            <div className="total-line" id="total-line">
              <span>总计:</span>
              <span id="total-amount">¥{getTotalPrice().toFixed(2)}</span>
            </div>
          </div>

          <div className="checkout-section" id="checkout-section">
            <button
              id="checkout-button"
              className="checkout-btn"
              onClick={checkout}
              disabled={isProcessing}
              data-action="checkout"
            >
              {isProcessing ? '处理中...' : '立即支付'}
            </button>
          </div>
        </section>
      )}
    </div>
  );
};

export default ShoppingCart;