import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

function PerformanceTest() {
  const [isRunning, setIsRunning] = useState(false);
  const [testResults, setTestResults] = useState<string[]>([]);

  const addResult = (result: string) => {
    setTestResults(prev => [...prev.slice(-4), result]); // 只保留最近5条结果
  };

  // 长任务测试 - 阻塞主线程
  const runLongTask = (duration: number = 100) => {
    setIsRunning(true);
    addResult(`🔄 开始执行 ${duration}ms 长任务...`);

    const start = performance.now();

    // 使用 setTimeout 让界面更新后再执行长任务
    setTimeout(() => {
      while (performance.now() - start < duration) {
        // 故意阻塞主线程
        Math.random();
      }

      const actualDuration = performance.now() - start;
      addResult(`✅ 长任务完成: 实际耗时 ${actualDuration.toFixed(2)}ms`);
      setIsRunning(false);

      // 手动记录性能指标
      (window as any).monitor?.trackMetric('long_task_duration', actualDuration, {
        type: 'manual_test',
        expected: duration
      });
    }, 10);
  };

  // CPU 密集型计算
  const runCPUIntensiveTask = () => {
    setIsRunning(true);
    addResult('🔄 开始 CPU 密集型计算...');

    setTimeout(() => {
      const start = performance.now();

      // 计算斐波那契数列（递归方式，CPU 密集）
      function fibonacci(n: number): number {
        if (n <= 1) return n;
        return fibonacci(n - 1) + fibonacci(n - 2);
      }

      const result = fibonacci(35); // 这会花费较长时间
      const duration = performance.now() - start;

      addResult(`✅ 计算完成: fib(35)=${result}, 耗时 ${duration.toFixed(2)}ms`);
      setIsRunning(false);

      (window as any).monitor?.trackMetric('cpu_intensive_task', duration, {
        type: 'fibonacci',
        input: 35,
        result: result
      });
    }, 10);
  };

  // 内存密集型测试
  const runMemoryTest = () => {
    setIsRunning(true);
    addResult('🔄 开始内存分配测试...');

    setTimeout(() => {
      const start = performance.now();

      // 创建大量对象
      const largeArray: number[][] = [];
      for (let i = 0; i < 1000; i++) {
        largeArray.push(new Array(1000).fill(Math.random()));
      }

      const duration = performance.now() - start;
      const memoryUsed = (largeArray.length * 1000 * 8) / (1024 * 1024); // 估算MB

      addResult(`✅ 内存测试完成: 分配 ${memoryUsed.toFixed(2)}MB, 耗时 ${duration.toFixed(2)}ms`);

      // 立即清理内存
      largeArray.length = 0;

      setIsRunning(false);

      (window as any).monitor?.trackMetric('memory_allocation', memoryUsed, {
        type: 'array_allocation',
        duration: duration
      });
    }, 10);
  };

  // DOM 操作密集型测试
  const runDOMTest = () => {
    setIsRunning(true);
    addResult('🔄 开始 DOM 操作测试...');

    setTimeout(() => {
      const start = performance.now();
      const container = document.createElement('div');
      document.body.appendChild(container);

      // 创建大量 DOM 元素
      for (let i = 0; i < 1000; i++) {
        const element = document.createElement('div');
        element.textContent = `测试元素 ${i}`;
        element.style.position = 'absolute';
        element.style.left = `${Math.random() * 100}px`;
        element.style.top = `${Math.random() * 100}px`;
        container.appendChild(element);
      }

      const duration = performance.now() - start;
      addResult(`✅ DOM 测试完成: 创建1000个元素, 耗时 ${duration.toFixed(2)}ms`);

      // 清理 DOM
      document.body.removeChild(container);
      setIsRunning(false);

      (window as any).monitor?.trackMetric('dom_operation', duration, {
        type: 'element_creation',
        count: 1000
      });
    }, 10);
  };

  // 网络请求性能测试
  const runNetworkTest = async () => {
    setIsRunning(true);
    addResult('🔄 开始网络请求性能测试...');

    const requests = [
      'http://localhost:3001/api/test/success',
      'http://localhost:3001/api/debug/stats',
    ];

    const results: number[] = [];

    for (const url of requests) {
      try {
        const start = performance.now();
        const response = await fetch(url);
        const duration = performance.now() - start;

        results.push(duration);
        await response.json(); // 确保完整接收响应

        addResult(`📡 请求完成: ${duration.toFixed(2)}ms - ${response.status}`);
      } catch (error) {
        addResult(`❌ 请求失败: ${url}`);
      }
    }

    const avgDuration = results.reduce((a, b) => a + b, 0) / results.length;
    addResult(`✅ 网络测试完成: 平均耗时 ${avgDuration.toFixed(2)}ms`);

    (window as any).monitor?.trackMetric('network_performance', avgDuration, {
      type: 'api_requests',
      count: results.length
    });

    setIsRunning(false);
  };

  // 获取导航时序 - 修复API兼容性问题
  const collectPageMetrics = () => {
    if ('performance' in window) {
      // 优先使用新API
      const navigation = performance.getEntriesByType('navigation')[0] as PerformanceNavigationTiming;
      const paint = performance.getEntriesByType('paint');

      if (navigation) {
        const metrics = {
          'DNS解析': navigation.domainLookupEnd - navigation.domainLookupStart,
          'TCP连接': navigation.connectEnd - navigation.connectStart,
          '页面加载': navigation.loadEventEnd - navigation.fetchStart, // 修复：使用 fetchStart 替代 navigationStart
          'DOM准备': navigation.domContentLoadedEventEnd - navigation.fetchStart, // 修复：使用 fetchStart
        };

        addResult('📊 页面性能指标:');
        Object.entries(metrics).forEach(([name, value]) => {
          addResult(`  ${name}: ${value.toFixed(2)}ms`);
          (window as any).monitor?.trackMetric(`page_${name}`, value, { type: 'navigation' });
        });
      } else {
        // 降级到旧的 performance.timing API
        const timing = performance.timing;
        if (timing) {
          const metrics = {
            'DNS解析': timing.domainLookupEnd - timing.domainLookupStart,
            'TCP连接': timing.connectEnd - timing.connectStart,
            '页面加载': timing.loadEventEnd - timing.navigationStart,
            'DOM准备': timing.domContentLoadedEventEnd - timing.navigationStart,
          };

          addResult('📊 页面性能指标 (兼容模式):');
          Object.entries(metrics).forEach(([name, value]) => {
            addResult(`  ${name}: ${value.toFixed(2)}ms`);
            (window as any).monitor?.trackMetric(`page_${name}`, value, { type: 'navigation_legacy' });
          });
        }
      }

      paint.forEach(entry => {
        addResult(`🎨 ${entry.name}: ${entry.startTime.toFixed(2)}ms`);
        (window as any).monitor?.trackMetric(entry.name.replace('-', '_'), entry.startTime, { type: 'paint' });
      });
    }
  };

  // 组件挂载时自动收集性能数据
  useEffect(() => {
    collectPageMetrics();
  }, []);

  return (
    <div className="test-section">
      <h2>⚡ 性能监控测试</h2>

      <div style={{ marginBottom: '20px' }}>
        <Link to="/">
          <button className="test-button">← 返回首页</button>
        </Link>
      </div>

      {isRunning && (
        <div className="test-status info">
          🔄 性能测试执行中，请稍候...
        </div>
      )}

      <div style={{ marginBottom: '20px' }}>
        <h3>🐌 长任务测试</h3>
        <button
          className="test-button"
          onClick={() => runLongTask(50)}
          disabled={isRunning}
        >
          执行 50ms 长任务
        </button>
        <button
          className="test-button"
          onClick={() => runLongTask(100)}
          disabled={isRunning}
        >
          执行 100ms 长任务
        </button>
        <button
          className="test-button danger"
          onClick={() => runLongTask(200)}
          disabled={isRunning}
        >
          执行 200ms 长任务
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🖥️ 计算密集型测试</h3>
        <button
          className="test-button"
          onClick={runCPUIntensiveTask}
          disabled={isRunning}
        >
          CPU 密集计算（斐波那契）
        </button>
        <button
          className="test-button"
          onClick={runMemoryTest}
          disabled={isRunning}
        >
          内存分配测试
        </button>
        <button
          className="test-button"
          onClick={runDOMTest}
          disabled={isRunning}
        >
          DOM 操作测试
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🌐 网络性能测试</h3>
        <button
          className="test-button"
          onClick={runNetworkTest}
          disabled={isRunning}
        >
          网络请求性能测试
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>📊 页面性能指标</h3>
        <button
          className="test-button"
          onClick={collectPageMetrics}
        >
          重新收集页面指标
        </button>
      </div>

      {testResults.length > 0 && (
        <div style={{ marginBottom: '20px' }}>
          <h3>📝 测试结果</h3>
          <div style={{
            background: '#f5f5f5',
            padding: '12px',
            borderRadius: '4px',
            fontSize: '12px',
            fontFamily: 'monospace',
            maxHeight: '200px',
            overflowY: 'auto'
          }}>
            {testResults.map((result, index) => (
              <div key={index}>{result}</div>
            ))}
          </div>
        </div>
      )}

      <div>
        <h3>📊 观察要点</h3>
        <ul style={{ textAlign: 'left', lineHeight: '1.6', fontSize: '14px' }}>
          <li><strong>Performance 面板:</strong> 查看长任务是否被正确记录</li>
          <li><strong>Console 日志:</strong> 观察 SDK 的性能监控日志</li>
          <li><strong>Network 面板:</strong> 确认性能数据上报</li>
          <li><strong>内存使用:</strong> 使用 Memory 面板观察内存变化</li>
          <li><strong>FPS 监控:</strong> 长任务期间页面是否卡顿</li>
          <li><strong>指标准确性:</strong> 验证上报的性能指标是否准确</li>
        </ul>
      </div>
    </div>
  );
}

export default PerformanceTest;