import React, { useState } from 'react';
import { Link } from 'react-router-dom';

// 测试组件 - 会抛出渲染错误
function ErrorComponent({ shouldError }: { shouldError: boolean }) {
  if (shouldError) {
    // 故意抛出错误来测试 ErrorBoundary
    throw new Error('React 组件渲染错误 - 这是一个测试错误');
  }
  return <div className="test-status success">✅ 组件正常渲染</div>;
}

function ErrorTest() {
  const [renderError, setRenderError] = useState(false);
  const [lastError, setLastError] = useState<string>('');

  // JavaScript 运行时错误
  const triggerJSError = () => {
    setLastError('JavaScript 运行时错误');
    // 访问未定义的变量
    (window as any).undefinedVariable.nonExistentMethod();
  };

  // Promise rejection 错误
  const triggerPromiseError = () => {
    setLastError('Promise Rejection 错误');
    Promise.reject(new Error('测试 Promise rejection 错误'));
  };

  // 语法错误（通过 eval）
  const triggerSyntaxError = () => {
    setLastError('语法错误');
    try {
      eval('var x = ;'); // 故意的语法错误
    } catch (e) {
      throw e; // 重新抛出让 SDK 捕获
    }
  };

  // 异步错误
  const triggerAsyncError = () => {
    setLastError('异步错误');
    setTimeout(() => {
      throw new Error('异步执行中的错误');
    }, 100);
  };

  // 网络错误
  const triggerNetworkError = () => {
    setLastError('网络请求错误');
    fetch('/non-existent-api-endpoint')
      .catch(error => {
        console.error('网络请求失败:', error);
      });
  };

  // 资源加载错误
  const triggerResourceError = () => {
    setLastError('资源加载错误');
    const img = new Image();
    img.src = '/non-existent-image.jpg';
    document.body.appendChild(img);

    // 创建脚本加载错误
    const script = document.createElement('script');
    script.src = '/non-existent-script.js';
    document.head.appendChild(script);
  };

  // React 渲染错误
  const triggerRenderError = () => {
    setLastError('React 渲染错误');
    setRenderError(true);
  };

  // 手动上报错误
  const manualReportError = () => {
    setLastError('手动上报错误');
    const error = new Error('这是一个手动上报的白屏测试错误，captureError 方法应该被调用');
    error.stack = `Error: 这是一个手动上报的测试错误
    at triggerManualError (ErrorTest.tsx:${Math.floor(Math.random() * 100)}:${Math.floor(Math.random() * 50)})
    at onClick (ErrorTest.tsx:${Math.floor(Math.random() * 100)}:${Math.floor(Math.random() * 50)})
    at HTMLButtonElement.callCallback (react-dom.development.js:${Math.floor(Math.random() * 1000)}:${Math.floor(Math.random() * 100)})`;

    (window as any).monitor?.captureError(error, {
      errorLevel: 'high',
      component: 'ErrorTest',
      userAction: 'manual_report',
      timestamp: Date.now()
    });
  };

  const triggerWhiteScreen = () => {
    setLastError('白屏检测测试');

    // 直接手动上报白屏错误，而不是依赖自动检测
    if ((window as any).monitor) {
      const whiteScreenError = new Error('White screen detected - Manual test trigger');
      (window as any).monitor.captureError(whiteScreenError, {
        errorType: 'white_screen_test',
        detectionType: 'manual',
        timestamp: Date.now()
      });
      console.log('✅ 手动触发白屏错误上报');
    } else {
      console.error('❌ Monitor实例不存在');
    }
  };
  // 长任务模拟（会被性能监控捕获）
  const triggerLongTask = () => {
    setLastError('长任务执行（性能问题）');
    const start = Date.now();
    // 模拟200ms的同步阻塞操作
    while (Date.now() - start < 200) {
      // 空循环，阻塞主线程
    }
  };

  return (
    <div className="test-section">
      <h2>🔴 错误监控测试</h2>

      <div style={{ marginBottom: '20px' }}>
        <Link to="/">
          <button className="test-button">← 返回首页</button>
        </Link>
      </div>

      {lastError && (
        <div className="test-status error">
          ⚠️ 最近触发的错误: {lastError}
        </div>
      )}

      <div style={{ marginBottom: '20px' }}>
        <h3>🚨 JavaScript 错误测试</h3>
        <button className="test-button danger" onClick={triggerJSError}>
          触发运行时错误
        </button>
        <button className="test-button danger" onClick={triggerSyntaxError}>
          触发语法错误
        </button>
        <button className="test-button danger" onClick={triggerAsyncError}>
          触发异步错误
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🌐 Promise 和网络错误</h3>
        <button className="test-button danger" onClick={triggerPromiseError}>
          Promise Rejection
        </button>
        <button className="test-button danger" onClick={triggerNetworkError}>
          网络请求错误
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>📁 资源加载错误</h3>
        <button className="test-button danger" onClick={triggerResourceError}>
          触发资源加载失败
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>⚛️ React 组件错误</h3>
        <button
          className="test-button danger"
          onClick={triggerRenderError}
          disabled={renderError}
        >
          触发组件渲染错误
        </button>
        {renderError && <ErrorComponent shouldError={true} />}
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🎯 特殊场景测试</h3>
        <button className="test-button danger" onClick={manualReportError}>
          手动上报错误
        </button>
        <button className="test-button danger" onClick={triggerWhiteScreen}>
          触发白屏检测（3秒）
        </button>
        <button className="test-button danger" onClick={triggerLongTask}>
          触发长任务（200ms）
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🧪 批量错误测试</h3>
        <button
          className="test-button danger"
          onClick={() => {
            // 快速触发多个错误，测试去重功能
            for (let i = 0; i < 5; i++) {
              setTimeout(() => {
                throw new Error(`批量测试错误 #${i + 1}`);
              }, i * 100);
            }
            setLastError('批量错误测试（5个错误）');
          }}
        >
          批量错误测试（测试去重）
        </button>
      </div>

      <div>
        <h3>📊 观察要点</h3>
        <ul style={{ textAlign: 'left', lineHeight: '1.6', fontSize: '14px' }}>
          <li><strong>浏览器 Console:</strong> 查看 SDK 的错误捕获日志</li>
          <li><strong>Network 面板:</strong> 观察错误数据的上报请求</li>
          <li><strong>Mock 服务器:</strong> 查看终端输出的详细错误信息</li>
          <li><strong>错误内容:</strong> 验证堆栈信息、文件名、行号等是否准确</li>
          <li><strong>面包屑:</strong> 确认用户操作轨迹是否完整记录</li>
          <li><strong>去重机制:</strong> 相同错误是否会被正确去重</li>
        </ul>
      </div>
    </div>
  );
}

export default ErrorTest;