// src/App.tsx - 修复版本，集成所有测试组件
import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import TestComponents from './components/TestComponents';
import UserDashboard from './components/UserDashboard';
import ShoppingCart from './components/ShoppingCart';
import ErrorTest from './components/ErrorTest';
import PerformanceTest from './components/PerformanceTest';
import BehaviorTest from './components/BehaviorTest';

// 简化的 ErrorBoundary
class SimpleErrorBoundary extends React.Component<
  { children: React.ReactNode },
  { hasError: boolean }
> {
  constructor(props: any) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError(): { hasError: boolean } {
    return { hasError: true };
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    console.error('ErrorBoundary 捕获到错误:', error, errorInfo);

    // 尝试使用 SDK 上报错误
    if ((window as any).monitor) {
      (window as any).monitor.captureError(error, {
        component: 'ErrorBoundary',
        errorInfo: errorInfo
      });
    }
  }

  render() {
    if (this.state.hasError) {
      return (
        <div style={{ padding: '20px', textAlign: 'center' }}>
          <h2>抱歉，页面出现了错误</h2>
          <p>请刷新页面重试</p>
          <button onClick={() => window.location.reload()}>刷新页面</button>
        </div>
      );
    }

    return this.props.children;
  }
}

function App() {
  return (
    <SimpleErrorBoundary>
      <Router>
        <div className="app-container" id="app-root">
          <header className="app-header" id="main-header">
            <h1 id="app-title">Mini Sentry SDK 监控测试平台</h1>
            <nav id="main-navigation">
              <Link to="/" id="nav-home">首页</Link>
              <Link to="/dashboard" id="nav-dashboard">用户面板</Link>
              <Link to="/shop" id="nav-shop">购物车</Link>
              <Link to="/error-test" id="nav-error-test">错误测试</Link>
              <Link to="/performance-test" id="nav-performance-test">性能测试</Link>
              <Link to="/behavior-test" id="nav-behavior-test">行为测试</Link>
              <Link to="/test" id="nav-test">综合测试</Link>
            </nav>
          </header>

          <main className="app-main" id="main-content">
            <Routes>
              <Route path="/" element={<HomePage />} />
              <Route path="/dashboard" element={<UserDashboard />} />
              <Route path="/shop" element={<ShoppingCart />} />
              <Route path="/error-test" element={<ErrorTest />} />
              <Route path="/performance-test" element={<PerformanceTest />} />
              <Route path="/behavior-test" element={<BehaviorTest />} />
              <Route path="/test" element={<TestComponents />} />
            </Routes>
          </main>

          <footer className="app-footer" id="main-footer" style={{
            background: '#f5f5f5',
            padding: '16px 24px',
            textAlign: 'center',
            marginTop: '40px',
            borderTop: '1px solid #e8e8e8'
          }}>
            <p id="copyright">© 2025 Mini Sentry 监控平台测试</p>
          </footer>
        </div>
      </Router>
    </SimpleErrorBoundary>
  );
}

// 首页组件 - 增加 LCP 指标监测元素
function HomePage() {
  const [userInfo, setUserInfo] = useState({
    name: '',
    email: '',
    age: ''
  });

  const [lcpImageLoaded, setLcpImageLoaded] = useState(false);

  const handleInputChange = (field: string, value: string) => {
    setUserInfo(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // 模拟API调用
    try {
      const response = await fetch('/api/user/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(userInfo)
      });

      if (!response.ok) {
        throw new Error(`注册失败: ${response.status}`);
      }

      alert('注册成功！');
    } catch (error) {
      console.error('注册过程中发生错误:', error);
    }
  };

  return (
    <div className="home-page" id="home-container">
      {/* LCP 候选元素 - 大型英雄横幅 */}
      <section className="hero-section" id="hero-banner" style={{
        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
        color: 'white',
        padding: '80px 40px',
        textAlign: 'center',
        borderRadius: '12px',
        margin: '20px 0 40px 0',
        boxShadow: '0 10px 30px rgba(0,0,0,0.2)'
      }}>
        {/* 大型 LCP 图片元素 */}
        <div style={{ marginBottom: '30px' }}>
          <img
            id="hero-lcp-image"
            src="https://picsum.photos/600/300?random=1"
            alt="Mini Sentry SDK Hero Banner"
            style={{
              width: '100%',
              maxWidth: '600px',
              height: '300px',
              objectFit: 'cover',
              borderRadius: '8px',
              border: '3px solid rgba(255,255,255,0.3)'
            }}
            onLoad={() => {
              setLcpImageLoaded(true);
              console.log('🎯 LCP 候选图片已加载完成');

              // 手动记录 LCP 相关事件
              if ((window as any).monitor) {
                (window as any).monitor.trackEvent('performance', 'LCP候选元素加载完成', {
                  element: 'hero-lcp-image',
                  timestamp: Date.now(),
                  loadTime: performance.now()
                });
              }
            }}
          />
        </div>

        <h2 id="hero-title" style={{
          fontSize: '3rem',
          margin: '20px 0',
          textShadow: '0 2px 4px rgba(0,0,0,0.3)'
        }}>
          欢迎使用 Mini Sentry 监控平台
        </h2>

        <p id="hero-description" style={{
          fontSize: '1.2rem',
          margin: '20px 0 40px 0',
          opacity: 0.9
        }}>
          实时监控您的应用性能和错误，提供完整的用户体验分析
        </p>

        <button
          id="hero-cta-button"
          className="cta-button"
          style={{
            background: 'white',
            color: '#667eea',
            border: 'none',
            padding: '16px 32px',
            fontSize: '1.1rem',
            fontWeight: 'bold',
            borderRadius: '8px',
            cursor: 'pointer',
            boxShadow: '0 4px 15px rgba(0,0,0,0.2)',
            transition: 'transform 0.2s, box-shadow 0.2s'
          }}
          onMouseEnter={(e) => {
            e.currentTarget.style.transform = 'translateY(-2px)';
            e.currentTarget.style.boxShadow = '0 6px 20px rgba(0,0,0,0.3)';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.transform = 'translateY(0)';
            e.currentTarget.style.boxShadow = '0 4px 15px rgba(0,0,0,0.2)';
          }}
          onClick={() => {
            // 手动触发一个自定义事件
            console.log('用户点击了主要CTA按钮');
            if ((window as any).monitor) {
              (window as any).monitor.trackEvent('user', '点击主要CTA按钮', {
                button: 'hero-cta',
                timestamp: Date.now(),
                lcpImageLoaded: lcpImageLoaded
              });
            }
          }}
        >
          🚀 开始监控
        </button>

        {/* LCP 状态指示器 */}
        <div style={{
          marginTop: '20px',
          padding: '8px 16px',
          background: lcpImageLoaded ? 'rgba(76, 175, 80, 0.2)' : 'rgba(255, 193, 7, 0.2)',
          borderRadius: '20px',
          fontSize: '0.9rem',
          border: `1px solid ${lcpImageLoaded ? 'rgba(76, 175, 80, 0.5)' : 'rgba(255, 193, 7, 0.5)'}`
        }}>
          {lcpImageLoaded ? '✅ LCP 元素已加载' : '⏳ LCP 元素加载中...'}
        </div>
      </section>

      {/* 添加额外的大型内容块作为 LCP 候选 */}
      <section className="feature-highlight" id="feature-highlight" style={{
        background: 'white',
        padding: '60px 40px',
        borderRadius: '12px',
        boxShadow: '0 4px 20px rgba(0,0,0,0.1)',
        marginBottom: '40px'
      }}>
        <h2 style={{ textAlign: 'center', marginBottom: '30px', fontSize: '2.5rem', color: '#333' }}>
          🔍 核心功能特性
        </h2>

        <div className="features-grid" style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
          gap: '30px',
          marginTop: '40px'
        }}>
          <div className="feature-card" style={{
            padding: '30px',
            background: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
            color: 'white',
            borderRadius: '12px',
            textAlign: 'center',
            boxShadow: '0 8px 25px rgba(240, 147, 251, 0.3)'
          }}>
            <div style={{ fontSize: '3rem', marginBottom: '20px' }}>🚨</div>
            <h4 style={{ fontSize: '1.5rem', marginBottom: '15px' }}>错误监控</h4>
            <p style={{ opacity: 0.9, lineHeight: '1.6' }}>
              实时捕获和分析应用错误，包括 JavaScript 错误、Promise 异常、资源加载失败等
            </p>
          </div>

          <div className="feature-card" style={{
            padding: '30px',
            background: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)',
            color: 'white',
            borderRadius: '12px',
            textAlign: 'center',
            boxShadow: '0 8px 25px rgba(79, 172, 254, 0.3)'
          }}>
            <div style={{ fontSize: '3rem', marginBottom: '20px' }}>⚡</div>
            <h4 style={{ fontSize: '1.5rem', marginBottom: '15px' }}>性能监控</h4>
            <p style={{ opacity: 0.9, lineHeight: '1.6' }}>
              监控页面加载性能指标，包括 LCP、FID、CLS 等关键 Web Vitals 指标
            </p>
          </div>

          <div className="feature-card" style={{
            padding: '30px',
            background: 'linear-gradient(135deg, #fa709a 0%, #fee140 100%)',
            color: 'white',
            borderRadius: '12px',
            textAlign: 'center',
            boxShadow: '0 8px 25px rgba(250, 112, 154, 0.3)'
          }}>
            <div style={{ fontSize: '3rem', marginBottom: '20px' }}>👥</div>
            <h4 style={{ fontSize: '1.5rem', marginBottom: '15px' }}>用户行为</h4>
            <p style={{ opacity: 0.9, lineHeight: '1.6' }}>
              跟踪用户操作轨迹，分析用户行为模式，优化用户体验
            </p>
          </div>
        </div>
      </section>

      {/* 用户注册表单 */}
      <section className="user-registration" id="registration-section" style={{
        background: 'white',
        padding: '40px',
        borderRadius: '12px',
        boxShadow: '0 4px 20px rgba(0,0,0,0.1)',
        marginBottom: '40px'
      }}>
        <h3 id="registration-title" style={{ textAlign: 'center', marginBottom: '30px', fontSize: '2rem' }}>
          用户注册测试
        </h3>
        <form onSubmit={handleSubmit} id="user-registration-form" style={{ maxWidth: '500px', margin: '0 auto' }}>
          <div className="form-group" id="name-input-group" style={{ marginBottom: '20px' }}>
            <label htmlFor="user-name-input" style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold' }}>
              姓名:
            </label>
            <input
              type="text"
              id="user-name-input"
              value={userInfo.name}
              onChange={(e) => handleInputChange('name', e.target.value)}
              placeholder="请输入您的姓名"
              data-testid="name-field"
              style={{
                width: '100%',
                padding: '12px',
                border: '2px solid #e8e8e8',
                borderRadius: '8px',
                fontSize: '16px',
                transition: 'border-color 0.3s'
              }}
            />
          </div>

          <div className="form-group" id="email-input-group" style={{ marginBottom: '20px' }}>
            <label htmlFor="user-email-input" style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold' }}>
              邮箱:
            </label>
            <input
              type="email"
              id="user-email-input"
              value={userInfo.email}
              onChange={(e) => handleInputChange('email', e.target.value)}
              placeholder="请输入您的邮箱"
              data-testid="email-field"
              style={{
                width: '100%',
                padding: '12px',
                border: '2px solid #e8e8e8',
                borderRadius: '8px',
                fontSize: '16px',
                transition: 'border-color 0.3s'
              }}
            />
          </div>

          <div className="form-group" id="age-input-group" style={{ marginBottom: '30px' }}>
            <label htmlFor="user-age-input" style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold' }}>
              年龄:
            </label>
            <input
              type="number"
              id="user-age-input"
              value={userInfo.age}
              onChange={(e) => handleInputChange('age', e.target.value)}
              placeholder="请输入您的年龄"
              data-testid="age-field"
              style={{
                width: '100%',
                padding: '12px',
                border: '2px solid #e8e8e8',
                borderRadius: '8px',
                fontSize: '16px',
                transition: 'border-color 0.3s'
              }}
            />
          </div>

          <button
            type="submit"
            id="registration-submit-button"
            className="submit-button"
            style={{
              width: '100%',
              padding: '16px',
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              color: 'white',
              border: 'none',
              borderRadius: '8px',
              fontSize: '18px',
              fontWeight: 'bold',
              cursor: 'pointer',
              transition: 'transform 0.2s, box-shadow 0.2s'
            }}
          >
            立即注册
          </button>
        </form>
      </section>

      {/* 测试页面快速导航 */}
      <section className="quick-navigation" id="quick-navigation" style={{
        background: '#f8f9fa',
        padding: '40px',
        borderRadius: '12px',
        textAlign: 'center'
      }}>
        <h3 style={{ marginBottom: '30px', fontSize: '2rem' }}>🧪 快速测试导航</h3>
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))',
          gap: '20px'
        }}>
          <Link to="/error-test" style={{ textDecoration: 'none' }}>
            <div className="nav-card" style={{
              background: '#ff6b6b',
              color: 'white',
              padding: '20px',
              borderRadius: '8px',
              transition: 'transform 0.2s'
            }}>
              <div style={{ fontSize: '2rem', marginBottom: '10px' }}>🔴</div>
              <h4>错误测试</h4>
            </div>
          </Link>

          <Link to="/performance-test" style={{ textDecoration: 'none' }}>
            <div className="nav-card" style={{
              background: '#4ecdc4',
              color: 'white',
              padding: '20px',
              borderRadius: '8px',
              transition: 'transform 0.2s'
            }}>
              <div style={{ fontSize: '2rem', marginBottom: '10px' }}>⚡</div>
              <h4>性能测试</h4>
            </div>
          </Link>

          <Link to="/behavior-test" style={{ textDecoration: 'none' }}>
            <div className="nav-card" style={{
              background: '#45b7d1',
              color: 'white',
              padding: '20px',
              borderRadius: '8px',
              transition: 'transform 0.2s'
            }}>
              <div style={{ fontSize: '2rem', marginBottom: '10px' }}>👤</div>
              <h4>行为测试</h4>
            </div>
          </Link>
        </div>
      </section>

      <div>
        <h3 style={{ textAlign: 'center', marginTop: '40px', marginBottom: '20px' }}>📊 LCP 监控说明</h3>
        <div style={{
          background: '#e3f2fd',
          border: '1px solid #90caf9',
          borderRadius: '8px',
          padding: '20px',
          fontSize: '14px',
          lineHeight: '1.6'
        }}>
          <ul style={{ margin: 0, paddingLeft: '20px' }}>
            <li><strong>LCP 元素:</strong> 页面顶部的大型横幅图片 (600x300px) 是主要的 LCP 候选元素</li>
            <li><strong>监控方法:</strong> 打开开发者工具 → Performance 面板 → 录制页面加载过程</li>
            <li><strong>观察要点:</strong> 查看 LCP 指标是否指向横幅图片，以及加载完成时间</li>
            <li><strong>SDK 集成:</strong> 图片加载完成时会自动触发相关事件记录</li>
            <li><strong>最佳实践:</strong> LCP 应该在 2.5 秒内完成，这个测试图片使用 Data URL 确保快速加载</li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default App;