// src/main.tsx - 添加调试版本
import React from 'react';
import { createRoot } from 'react-dom/client';
import App from './App';

const config = {
  projectId: 'pro-52038057',
  apiUrl: 'http://192.168.1.161:8080/frontend',
  debug: true,  // 确保开启调试
  sampleRate: 1,
  maxBreadcrumbs: 20,
  version: '1.0.0-test'
};

async function initSDK() {
  try {
    const { Monitor } = await import('mini-sentry-sdk');

    const monitor = new Monitor({
      projectId: config.projectId,
      apiUrl: config.apiUrl,
      debug: config.debug,
      sampleRate: config.sampleRate,
      maxBreadcrumbs: config.maxBreadcrumbs,
      defaultTags: {
        environment: 'development',
        version: config.version,
        buildTime: new Date().toISOString(),
        userId: 'test-user-' + Date.now()
      },
      enabledModules: {
        error: true,
        performance: true,  // 确保性能监控开启
        behavior: true,
      }
    });

    await monitor.init();
    console.log('✅ SDK 初始化成功');

    // 挂载到全局
    (window as any).monitor = monitor;

    // 🔍 添加性能监控调试
    console.log('🔍 页面加载状态:', document.readyState);
    console.log('🔍 Performance API 可用:', 'performance' in window);

    // 延迟检查性能数据发送
    setTimeout(() => {
      console.log('🔍 5秒后检查 - 应该已经发送性能数据了');
      console.log('🔍 检查 Network 面板是否有 /performance 请求');
    }, 5000);

  } catch (error) {
    console.error('❌ SDK 初始化失败:', error);
    (window as any).monitor = {
      trackEvent: () => { },
      trackBusiness: () => { },
      trackMetric: () => { },
      captureError: () => { }
    };
  }
}

// 🔍 页面加载状态监控
console.log('🔍 开始加载，当前状态:', document.readyState);

document.addEventListener('DOMContentLoaded', () => {
  console.log('🔍 DOM 加载完成');
});

window.addEventListener('load', () => {
  console.log('🔍 页面完全加载完成');
});

initSDK();

const container = document.getElementById('root')!;
const root = createRoot(container);
root.render(<App />);